}

/*label, input [type='checkbox'] {*/
/*}*/

.sameLine {
    display: inline-block;
    width: 30%;
}

.overflowTable {
    overflow-x: auto;
}

ol.holidays {
    margin-top: 10px !important;
    padding-top: 10px !important;
    margin: 0;
