/* Create two equal columns that floats next to each other */
.column {
    float: left;
    width: 50%;
    padding: 20px;
}

/* Create two equal columns that floats next to each other */
.column4 {
    float: left;
    width: 25%;
    padding: 20px;
}

.column16 {
    float: left;
    width: 16%;
    padding: 20px;
}

.column33 {
    float: left;
    width: 33%;
    padding: 20px;
}

/* Clear floats after the columns */
.row:after {
    content: "";
    display: table;
    clear: both;
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
    .column {
        width: 100%;
    }
    .column4 {
        width: 100%;
    }
    .column16 {
        width: 100%;
    }
    .column33 {
        width: 100%;
    }
}

